// =============================================================================
// X402 PAYMENT VERIFICATION ABSTRACTION
// =============================================================================
// Unified verification interface that supports test, CDP, and disabled modes
// This abstraction makes it easy to swap between test and real CDP verification

import { NextRequest } from 'next/server';
import { verifyPaymentTestOnly, type TestVerificationResult } from './test-verification';
import { parseX402Header } from './payment-header-parser';
import { getCDPFacilitator, type CDPVerifyRequest, type CDPVerifyResponse } from './cdp-facilitator';
import { normalizeNetwork } from './amount-utils';
import { createLogger } from '@/lib/logger';

const logger = createLogger({ component: 'X402Verification' });

/**
 * Unified verification result
 * This interface is used regardless of verification mode (test, CDP, or disabled)
 */
export interface VerifyResult {
  success: boolean;
  x402TxHash?: string;
  facilitator: string;
  errorCode?: string;
}

/**
 * Request context for verification
 * Contains all information needed to verify a payment
 */
export interface RequestContext {
  request: NextRequest;
  paymentHeader?: string;
  paymentRequirements?: {
    scheme: string;
    network: string;
    maxAmountRequired: string;
    resource: string;
    payTo: string;
    asset: string;
    description?: string;
    mimeType?: string;
    maxTimeoutSeconds?: number;
    validAfter?: string;
    validBefore?: string;
    // EIP-712 domain info for USDC signature verification
    extra?: {
      name: string;
      version: string;
    };
  };
}

/**
 * Verify payment with CDP (production mode)
 * 
 * This implements real CDP verification - no faking, no mocks.
 * If CDP says no, the payment fails and is logged.
 * 
 * @param context - Request context with payment information
 * @returns Verification result
 */
async function verifyPaymentWithCDP(context: RequestContext): Promise<VerifyResult> {
  try {
    // Extract payment header from request if not provided in context
    const paymentHeader = context.paymentHeader || 
                         context.request.headers.get('x-payment') || 
                         context.request.headers.get('X-Payment') ||
                         '';

    // Debug: Log all headers to diagnose header forwarding issues
    const allHeaders: Record<string, string> = {};
    context.request.headers.forEach((value, key) => {
      // Mask sensitive headers
      if (key.toLowerCase() === 'x-payment' || key.toLowerCase() === 'authorization') {
        allHeaders[key] = value ? `${value.substring(0, 20)}... (length: ${value.length})` : 'EMPTY';
      } else if (!key.toLowerCase().includes('cookie')) {
        allHeaders[key] = value;
      }
    });
    
    logger.debug('CDP verification - checking payment header', {
      hasPaymentHeader: !!paymentHeader,
      paymentHeaderLength: paymentHeader?.length || 0,
      paymentHeaderPreview: paymentHeader ? `${paymentHeader.substring(0, 30)}...` : 'NONE',
      contextHasPaymentHeader: !!context.paymentHeader,
      requestHeaderKeys: Object.keys(allHeaders),
    });

    if (!paymentHeader) {
      logger.warn('No payment header provided for CDP verification', {
        contextPaymentHeader: context.paymentHeader ? 'provided' : 'missing',
        requestXPayment: context.request.headers.get('x-payment') ? 'present' : 'missing',
        requestXPaymentUpper: context.request.headers.get('X-Payment') ? 'present' : 'missing',
        allHeaderKeys: Object.keys(allHeaders),
      });
      return {
        success: false,
        facilitator: 'CDP',
        errorCode: 'PAYMENT_HEADER_MISSING',
      };
    }

    // Parse payment header
    const parseResult = parseX402Header(paymentHeader);
    if (!parseResult.valid || !parseResult.parsed) {
      logger.warn('Invalid payment header format', { error: parseResult.error });
      return {
        success: false,
        facilitator: 'CDP',
        errorCode: 'INVALID_PAYMENT_HEADER',
      };
    }

    const parsed = parseResult.parsed;

    // Payment requirements must be provided in context for CDP verification
    if (!context.paymentRequirements) {
      logger.error('Payment requirements must be provided for CDP verification');
      return {
        success: false,
        facilitator: 'CDP',
        errorCode: 'MISSING_PAYMENT_REQUIREMENTS',
      };
    }

    const requirements = context.paymentRequirements;

    // Validate required fields
    if (!requirements.resource) {
      logger.error('Missing resource in payment requirements');
      return {
        success: false,
        facilitator: 'CDP',
        errorCode: 'MISSING_RESOURCE',
      };
    }

    if (!requirements.asset) {
      logger.error('Missing asset in payment requirements');
      return {
        success: false,
        facilitator: 'CDP',
        errorCode: 'MISSING_ASSET',
      };
    }

    // Normalize network to CAIP-2 format (e.g., "base" -> "eip155:8453")
    const normalizedNetwork = normalizeNetwork(parsed.network || requirements.network || 'base');
    
    // Construct CDP verify request
    // The CDP facilitator will handle amount conversion to atomic units
    const cdpRequest: CDPVerifyRequest = {
      payment: paymentHeader,
      paymentPayload: {
        x402Version: 2,
        scheme: 'exact',
        network: normalizedNetwork, // Use CAIP-2 format: "eip155:8453"
        payload: {
          signature: parsed.signature,
          authorization: {
            from: parsed.authorization.from,
            to: parsed.authorization.to,
            value: parsed.authorization.value, // Will be converted to atomic units in CDP facilitator
            validAfter: parsed.authorization.validAfter,
            validBefore: parsed.authorization.validBefore,
            nonce: parsed.authorization.nonce,
          },
        },
      },
      paymentRequirements: {
        scheme: requirements.scheme || 'exact',
        network: normalizedNetwork, // Use CAIP-2 format: "eip155:8453"
        to: requirements.payTo,
        payTo: requirements.payTo,
        value: requirements.maxAmountRequired, // Will be converted to atomic units in CDP facilitator
        maxAmountRequired: requirements.maxAmountRequired,
        resource: requirements.resource,
        asset: requirements.asset,
        description: requirements.description,
        mimeType: requirements.mimeType,
        maxTimeoutSeconds: requirements.maxTimeoutSeconds,
        validAfter: requirements.validAfter,
        validBefore: requirements.validBefore,
        // EIP-712 domain info (name, version) for USDC signature verification
        extra: requirements.extra,
      },
    };

    logger.info('Calling CDP verifyPaymentWithRetry', {
      hasPaymentHeader: !!paymentHeader,
      hasPaymentRequirements: !!cdpRequest.paymentRequirements,
      network: cdpRequest.paymentPayload.network,
      resource: requirements.resource,
    });

    // Call CDP facilitator
    const cdp = getCDPFacilitator();
    const cdpResponse: CDPVerifyResponse = await cdp.verifyPaymentWithRetry(cdpRequest);

    // Map CDP response to VerifyResult
    if (cdpResponse.success && cdpResponse.valid) {
      logger.info('CDP verification succeeded', {
        transactionHash: cdpResponse.transactionHash,
        kytStatus: cdpResponse.kytStatus,
        ofacStatus: cdpResponse.ofacStatus,
      });

      return {
        success: true,
        x402TxHash: cdpResponse.transactionHash || parsed.txHash,
        facilitator: 'CDP',
      };
    } else {
      // CDP verification failed - log the error and return failure
      const errorCode = cdpResponse.error || 'CDP_VERIFICATION_FAILED';
      logger.warn('CDP verification failed', {
        error: cdpResponse.error,
        errorDetails: cdpResponse.errorDetails,
        kytStatus: cdpResponse.kytStatus,
        ofacStatus: cdpResponse.ofacStatus,
      });

      return {
        success: false,
        facilitator: 'CDP',
        errorCode: cdpResponse.error || 'CDP_VERIFICATION_FAILED',
      };
    }
  } catch (error) {
    logger.error('CDP verification error', { error });
    
    return {
      success: false,
      facilitator: 'CDP',
      errorCode: error instanceof Error ? error.message : 'CDP_VERIFICATION_ERROR',
    };
  }
}

/**
 * Verify payment disabled mode
 * 
 * Rejects all payments with a clear PAYMENT_DISABLED error.
 * 
 * @param context - Request context (unused, but kept for interface consistency)
 * @returns Verification result with PAYMENT_DISABLED error
 */
function verifyPaymentDisabled(context: RequestContext): VerifyResult {
  logger.warn('Payment verification is disabled', {
    hasPaymentHeader: !!context.paymentHeader,
  });

  return {
    success: false,
    facilitator: 'DISABLED',
    errorCode: 'PAYMENT_DISABLED',
  };
}

/**
 * Verify payment (unified interface)
 * 
 * This function routes to the appropriate verification method based on
 * environment configuration:
 * - X402_VERIFY_MODE="cdp": uses real CDP verification
 * - X402_VERIFY_MODE="test": uses test verification (for development)
 * - X402_VERIFY_MODE="off" or unset: rejects all payments with PAYMENT_DISABLED (default)
 * 
 * The X402CallLog schema does not need to change when switching modes because
 * both return the same VerifyResult interface.
 * 
 * @param context - Request context with payment information
 * @returns Verification result
 */
export async function verifyPayment(context: RequestContext): Promise<VerifyResult> {
  const verifyMode = process.env.X402_VERIFY_MODE?.toLowerCase();

  logger.debug('Verification mode', { verifyMode });

  if (verifyMode === 'cdp') {
    logger.debug('Using CDP verification mode');
    return await verifyPaymentWithCDP(context);
  }

  if (verifyMode === 'test') {
    logger.debug('Using test verification mode');
    const testResult = verifyPaymentTestOnly(context.request);
    
    // Convert TestVerificationResult to VerifyResult
    return {
      success: testResult.success,
      x402TxHash: testResult.x402TxHash,
      facilitator: testResult.facilitator || 'TEST_FACILITATOR',
      errorCode: testResult.errorCode,
    };
  }

  // Default: payments disabled (no fake success)
  logger.debug('Payment verification is disabled (default mode)');
  return verifyPaymentDisabled(context);
}

/**
 * Check if test mode is enabled
 */
export function isTestModeEnabled(): boolean {
  const verifyMode = process.env.X402_VERIFY_MODE?.toLowerCase();
  return verifyMode === 'test' || process.env.EASEPAY_TEST_MODE === 'true';
}

/**
 * Check if CDP mode is enabled
 */
export function isCDPModeEnabled(): boolean {
  const verifyMode = process.env.X402_VERIFY_MODE?.toLowerCase();
  return verifyMode === 'cdp';
}

/**
 * Check if payment verification is disabled
 */
export function isPaymentDisabled(): boolean {
  const verifyMode = process.env.X402_VERIFY_MODE?.toLowerCase();
  return verifyMode === 'off' || verifyMode === 'disabled';
}
