// =============================================================================
// URL ENRICHMENT METERED ENDPOINT
// =============================================================================
// Flagship metered demo endpoint with full x402 tracing
// Uses test-only verification (no real funds)

import { NextRequest, NextResponse } from 'next/server';
import { verifyPayment, type VerifyResult } from '@/integrations/x402/verification';
import { TEST_TOKEN_HEADER } from '@/integrations/x402/test-verification';
import { logX402Call } from '@/integrations/x402/x402-call-log';
import { getOrCreateRequestId, createRequestLogger } from '@/lib/request-id';
import { addRequestIdToResponse } from '@/lib/request-id';
import { createLogger } from '@/lib/logger';
import { normalizeNetwork } from '@/integrations/x402/amount-utils';

const logger = createLogger({ component: 'UrlEnrichMetered' });

/**
 * Hard-coded endpoint ID for URL enrichment demo
 */
const ENDPOINT_ID = 'ep_url_enrich_demo_v1';

/**
 * Fixed pricing: 0.01 test USDC on Base
 */
const PRICE_AMOUNT = '10000'; // 0.01 USDC (6 decimals: 10000 = 0.01 * 10^6)

/**
 * Chain ID for Base (CAIP-2 format)
 */
const CHAIN_ID = 'eip155:8453';

/**
 * Asset identifier
 * For test mode: 'USDC_TEST'
 * For CDP mode: Base USDC address '0x833589fCD6eDb6E08f4c7C32D4f71b54bdA02913'
 */
const getAsset = () => {
  const verifyMode = process.env.X402_VERIFY_MODE?.toLowerCase();
  return verifyMode === 'cdp' 
    ? '0x833589fCD6eDb6E08f4c7C32D4f71b54bdA02913' // Base USDC
    : 'USDC_TEST'; // Test asset
};
const ASSET = getAsset();

/**
 * Facilitator name for test mode
 */
const FACILITATOR = 'TEST_FACILITATOR';

/**
 * POST /api/v1/metered/url-enrich
 * 
 * ðŸš€ Flagship x402 Demo Endpoint - URL Enrichment
 * 
 * A production-ready example of a metered API endpoint with full x402 payment verification,
 * observability, and error handling. This endpoint demonstrates best practices for building
 * x402-enabled APIs.
 * 
 * Features:
 * - âœ… x402 payment verification (test or CDP mode)
 * - âœ… Full request/response tracing
 * - âœ… Comprehensive error handling
 * - âœ… Usage logging and metrics
 * - âœ… Deterministic URL enrichment
 * 
 * Headers:
 * - x-payment: <x402_payment_header> (required for CDP mode)
 * - x-easepay-test-token: demo-ok (required for test mode)
 * - x-conversation-id: <optional> (for request tracing)
 * - x-agent-id: <optional> (for agent identification)
 * 
 * Request Body:
 * {
 *   "url": "https://example.com"  // Required: Valid HTTP/HTTPS URL
 * }
 * 
 * Response (200 OK):
 * {
 *   "url": "https://example.com",
 *   "domain": "example.com",
 *   "title": "Example Domain",
 *   "summary": "This domain is for use in illustrative examples...",
 *   "enrichedAt": "2025-12-20T15:00:00.000Z",
 *   "requestId": "req_...",
 *   "x402": {
 *     "verified": true,
 *     "transactionHash": "0x...",
 *     "facilitator": "CDP"
 *   }
 * }
 * 
 * Response (402 Payment Required):
 * {
 *   "error": "Payment Required",
 *   "code": "PAYMENT_REQUIRED",
 *   "message": "This endpoint requires x402 payment verification...",
 *   "paymentDetails": { ... }
 * }
 */
export async function POST(request: NextRequest) {
  const requestId = getOrCreateRequestId(request);
  const requestLogger = createRequestLogger(requestId);
  const startTime = Date.now();
  
  // Extract tracing fields
  const conversationId = request.headers.get('x-conversation-id') || 
                         request.headers.get('x-request-id') || 
                         requestId;
  const agentId = request.headers.get('x-agent-id') || undefined;
  const customerId = request.headers.get('x-customer-id') || null;
  
  // Build resource URL - ensure www subdomain for production
  let baseUrl = process.env.NEXT_PUBLIC_APP_URL || 
                 (request.headers.get('x-forwarded-proto') === 'https' ? 'https' : 'http') + 
                 '://' + (request.headers.get('host') || 'localhost:3001');
  // Fix: Always use www subdomain for nexflowapp.app (non-www returns 404)
  if (baseUrl.includes('nexflowapp.app') && !baseUrl.includes('www.')) {
    baseUrl = baseUrl.replace('://nexflowapp.app', '://www.nexflowapp.app');
  }
  const resourceUrl = `${baseUrl}/api/v1/metered/url-enrich`;
  
  try {
    // Parse and validate request body
    let body: { url?: string };
    try {
      body = await request.json();
    } catch (error) {
      const latencyMs = Date.now() - startTime;
      await logX402Call({
        customerId,
        endpointId: ENDPOINT_ID,
        agentId: agentId || null,
        facilitator: FACILITATOR,
        chainId: CHAIN_ID,
        asset: ASSET,
        amount: PRICE_AMOUNT,
        status: 'failed',
        errorCode: 'INVALID_JSON',
        latencyMs,
        resource: resourceUrl,
      });
      
      return NextResponse.json(
        {
          error: 'Invalid JSON',
          code: 'INVALID_JSON',
          message: 'Request body must be valid JSON',
          requestId,
        },
        { status: 400 }
      );
    }

    const { url } = body;

    // Validate URL
    if (!url || typeof url !== 'string') {
      const latencyMs = Date.now() - startTime;
      await logX402Call({
        customerId,
        endpointId: ENDPOINT_ID,
        agentId: agentId || null,
        facilitator: FACILITATOR,
        chainId: CHAIN_ID,
        asset: ASSET,
        amount: PRICE_AMOUNT,
        status: 'failed',
        errorCode: 'INVALID_URL',
        latencyMs,
        resource: resourceUrl,
      });
      
      return NextResponse.json(
        {
          error: 'Invalid request',
          code: 'INVALID_URL',
          message: 'url field is required and must be a string',
          requestId,
        },
        { status: 400 }
      );
    }

    // Run x402 payment verification (uses abstraction - test or CDP based on config)
    const verifyResult: VerifyResult = await verifyPayment({
      request,
      paymentHeader: request.headers.get('x-payment') || undefined,
      paymentRequirements: {
        scheme: 'exact',
        network: normalizeNetwork('base'), // Use CAIP-2 format: "eip155:8453"
        maxAmountRequired: PRICE_AMOUNT,
        resource: resourceUrl,
        payTo: process.env.X402_RECIPIENT_ADDRESS || '0x0000000000000000000000000000000000000000',
        asset: ASSET,
        description: 'URL Enrichment Demo Endpoint',
        mimeType: 'application/json',
        maxTimeoutSeconds: 300,
        // EIP-712 domain info for USDC on Base (required for signature verification)
        extra: {
          name: 'USD Coin',
          version: '2',
        },
      },
    });

    if (!verifyResult.success) {
      const latencyMs = Date.now() - startTime;
      await logX402Call({
        customerId,
        endpointId: ENDPOINT_ID,
        agentId: agentId || null,
        facilitator: verifyResult.facilitator || FACILITATOR,
        chainId: CHAIN_ID,
        asset: ASSET,
        amount: PRICE_AMOUNT,
        status: 'failed',
        errorCode: verifyResult.errorCode || 'PAYMENT_REQUIRED',
        latencyMs,
        resource: resourceUrl,
      });
      
      // Return 402 Payment Required
      return NextResponse.json(
        {
          error: 'Payment Required',
          code: 'PAYMENT_REQUIRED',
          message: `This endpoint requires x402 payment verification. Include ${TEST_TOKEN_HEADER}: demo-ok header for test mode.`,
          paymentDetails: {
            scheme: 'exact',
            network: normalizeNetwork('base'), // Use CAIP-2 format: "eip155:8453"
            chainId: CHAIN_ID,
            asset: ASSET,
            amount: PRICE_AMOUNT,
            recipient: process.env.X402_RECIPIENT_ADDRESS || '0x0000000000000000000000000000000000000000',
            resource: resourceUrl,
            description: 'URL Enrichment Demo Endpoint',
          },
          requestId,
        },
        { status: 402 }
      );
    }

    // Payment verified - forward to internal handler
    requestLogger.info('Payment verified, forwarding to internal handler', {
      x402TxHash: verifyResult.x402TxHash,
      facilitator: verifyResult.facilitator,
    });

    // Call internal handler
    const internalUrl = `${baseUrl}/api/demo/url-enrich-internal`;
    let internalResponse: Response;
    try {
      internalResponse = await fetch(internalUrl, {
        method: 'POST',
        headers: {
          'Content-Type': 'application/json',
          'x-request-id': requestId,
        },
        body: JSON.stringify({ url }),
      });
    } catch (error) {
      const latencyMs = Date.now() - startTime;
      await logX402Call({
        customerId,
        endpointId: ENDPOINT_ID,
        agentId: agentId || null,
        facilitator: FACILITATOR,
        chainId: CHAIN_ID,
        asset: ASSET,
        amount: PRICE_AMOUNT,
        status: 'failed',
        errorCode: 'INTERNAL_HANDLER_ERROR',
        x402TxHash: verifyResult.x402TxHash,
        latencyMs,
        resource: resourceUrl,
      });
      
      requestLogger.error('Internal handler error', { error });
      
      return NextResponse.json(
        {
          error: 'Internal server error',
          code: 'INTERNAL_HANDLER_ERROR',
          message: error instanceof Error ? error.message : 'Failed to call internal handler',
          requestId,
        },
        { status: 500 }
      );
    }

    // Parse internal response
    let internalBody: any;
    try {
      internalBody = await internalResponse.json();
    } catch (error) {
      const latencyMs = Date.now() - startTime;
      await logX402Call({
        customerId,
        endpointId: ENDPOINT_ID,
        agentId: agentId || null,
        facilitator: FACILITATOR,
        chainId: CHAIN_ID,
        asset: ASSET,
        amount: PRICE_AMOUNT,
        status: 'failed',
        errorCode: 'INTERNAL_RESPONSE_ERROR',
        x402TxHash: verifyResult.x402TxHash,
        latencyMs,
        resource: resourceUrl,
      });
      
      requestLogger.error('Internal response parse error', { error });
      
      return NextResponse.json(
        {
          error: 'Internal server error',
          code: 'INTERNAL_RESPONSE_ERROR',
          message: 'Failed to parse internal handler response',
          requestId,
        },
        { status: 500 }
      );
    }

    // Check if internal handler returned an error
    if (!internalResponse.ok) {
      const latencyMs = Date.now() - startTime;
      await logX402Call({
        customerId,
        endpointId: ENDPOINT_ID,
        agentId: agentId || null,
        facilitator: FACILITATOR,
        chainId: CHAIN_ID,
        asset: ASSET,
        amount: PRICE_AMOUNT,
        status: 'failed',
        errorCode: internalBody.code || 'INTERNAL_HANDLER_FAILED',
        x402TxHash: verifyResult.x402TxHash,
        latencyMs,
        resource: resourceUrl,
      });
      
      return NextResponse.json(
        {
          ...internalBody,
          requestId,
        },
        { status: internalResponse.status }
      );
    }

    // Success - log and return enriched data
    const latencyMs = Date.now() - startTime;
    await logX402Call({
      customerId,
      endpointId: ENDPOINT_ID,
      agentId: agentId || null,
      facilitator: verifyResult.facilitator,
      chainId: CHAIN_ID,
      asset: ASSET,
      amount: PRICE_AMOUNT,
      status: 'success',
      x402TxHash: verifyResult.x402TxHash,
      latencyMs,
      resource: resourceUrl,
    });

    const response = NextResponse.json(
      {
        ...internalBody,
        requestId,
        x402: {
          verified: true,
          transactionHash: verifyResult.x402TxHash,
          facilitator: verifyResult.facilitator,
        },
      },
      {
        status: 200,
        headers: {
          'X-x402-Verified': 'true',
          'X-x402-TxHash': verifyResult.x402TxHash || '',
          'X-x402-Facilitator': verifyResult.facilitator,
        },
      }
    );

    addRequestIdToResponse(response, requestId);
    return response;

  } catch (error) {
    const latencyMs = Date.now() - startTime;
    const errorCode = 'INTERNAL_ERROR';
    const errorMessage = error instanceof Error ? error.message : 'Unknown error';
    
    // Try to log the failure
    try {
      await logX402Call({
        customerId,
        endpointId: ENDPOINT_ID,
        agentId: agentId || null,
        facilitator: FACILITATOR,
        chainId: CHAIN_ID,
        asset: ASSET,
        amount: PRICE_AMOUNT,
        status: 'failed',
        errorCode,
        latencyMs,
        resource: resourceUrl,
      });
    } catch (logError) {
      // Ignore logging errors
    }
    
    logger.error('URL enrich metered endpoint error', { 
      error,
      responseTime: latencyMs,
      failureCode: errorCode,
    });
    
    const response = NextResponse.json(
      {
        error: 'Internal server error',
        code: errorCode,
        details: errorMessage,
        requestId,
      },
      { status: 500 }
    );
    
    addRequestIdToResponse(response, requestId);
    return response;
  }
}

